import { create } from 'apisauce'
import './transactions.style.css'
import {  useEffect, useState } from "react";
import {RequestQueryBuilder} from '@nestjsx/crud-request'

const api = create({
    baseURL: 'http://68.183.124.193:9001',
})
function Transactions() {
    const [response, setResponse] = useState({
        data: [],
        count: 0,
        total: 1,
        pageCount: 1
    })
    const [page, setPage] = useState(1)

    useEffect(()=>{
        const loadTransactions = async ()=>{
            const queryString = RequestQueryBuilder.create({
                sort: [{ field: "dateTime", order: "DESC" }],
                page,
                limit: 30,
                resetCache: true
            }).query();
            const { data, status } = await api.get(`/credit?${queryString}`)
            if (status === 200) {
                setResponse(data)
            }

        }
        loadTransactions()
    }, [page])
    return (
        <div className="wrapper rounded">
            <nav className="navbar navbar-expand-lg navbar-dark dark d-lg-flex align-items-lg-start"><a
                className="navbar-brand" href="#">Transactions <p className="text-muted pl-1">Welcome to your
                transactions</p></a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav"
                        aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation"><span
                    className="navbar-toggler-icon"></span></button>
                <div className="collapse navbar-collapse" id="navbarNav">
                    <ul className="navbar-nav ml-lg-auto">
                        <li className="nav-item"><a className="nav-link" href="#"><span
                            className="fa fa-bell-o font-weight-bold"></span> <span
                            className="notify">Notifications</span> </a></li>
                        <li className="nav-item "><a href="#"><span className="fa fa-search"></span></a>
                            <input type="search" className="dark" placeholder="Search"/></li>
                    </ul>
                </div>
            </nav>

            <div className="d-flex justify-content-between align-items-center mt-3">
                <ul className="nav nav-tabs w-75">
                    <li className="nav-item"><a className="nav-link active" href="#history">History</a></li>
                </ul>
            </div>
            <div className="table-responsive mt-3">
                <table className="table table-dark table-borderless">
                    <thead>
                    <tr>
                        <th scope="col">Sender</th>
                        <th scope="col">Reference</th>
                        <th scope="col">Date</th>
                        <th scope="col" className="text-right">Amount</th>
                        <th scope="col">Narration</th>
                    </tr>
                    </thead>
                    <tbody>

                    {response.data.map((transaction)=>{
                        return <tr key={transaction.id}>
                            <td scope="row"> {transaction.accountNumber}</td>
                            <td >{transaction.reference}</td>
                            <td className="text-muted">{transaction.dateTime}</td>
                            <td className="d-flex justify-content-end align-items-center"><span
                              className="fa fa-long-arrow-down mr-1"></span> &#8358; {transaction.amount}
                            </td>
                            <td >
                                <div style={{
                                  whiteSpace: 'nowrap',
                                  overflow: 'hidden',
                                  display: 'block',
                                  textOverflow: 'ellipsis',
                                    width: '20vw'
                                }}>
                                    {transaction.narration}
                                </div>
                            </td>
                        </tr>
                    })}


                    </tbody>
                </table>
            </div>
            <div className="d-flex justify-content-between align-items-center results"><span className="pl-md-3">Showing<b
                className="text-white"> 1-{response.count} 0f {response.total} </b> transactions</span>
                <div className="pt-3">
                    <nav aria-label="Page navigation example">
                        <ul className="pagination">
                            <li onClick={()=>{
                                if (page > 0) {
                                    setPage(page - 1)
                                }
                            }} className="page-item disabled"><a className="page-link" href="#" aria-label="Previous">
                                <span aria-hidden="true">&lt;</span> </a></li>
                            <li onClick={()=>{
                              if (page < response.pageCount) {
                                  setPage(page + 1)
                              }
                            }} className="page-item"><a className="page-link" href="#" aria-label="Next"> <span
                                aria-hidden="true">&gt;</span> </a></li>
                        </ul>
                    </nav>
                </div>
            </div>
        </div>
    );
}

export default Transactions
